(function() {
    'use strict';

    angular
        .module('oncoscape', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'ui-rangeSlider', 'toastr']);

})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osTools', tools);

    /** @ngInject */
    function tools() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/tools/tools.html',
            controller: ToolsController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function ToolsController(osApi, $state, $stateParams) {

            if (angular.isUndefined($stateParams.datasource)){
                $state.go("datasource");
                return;
            }

            var vm = this;
            vm.datasource = $stateParams.datasource;
            vm.tools = [{
                name: 'Markers + Patients',
                route: 'markers',
                img: 'markers.png',
                copy: 'Link copy number variation and mutation data to patients.'
            }, {
                name: 'Timelines',
                route: 'timelines',
                img: 'timelines.png',
                copy: ''
            }, {
                name: 'Pathways',
                route: 'pathways',
                img: 'pathways.png',
                copy: 'Map patient specific expression levels on a hand curated network of genes.'
            }, {
                name: 'PLSR',
                route: 'plsr',
                img: 'plsr.png',
                copy: 'Use linear regression to correlate genes with clinical features using RNA expression.'
            }, {
                name: 'PCA',
                route: 'pca',
                img: 'pca.png',
                copy: 'Two dimensional view of per sample expression data.'
            }, {
                name: 'Survival',
                route: 'survival',
                img: 'survival.png',
                copy: 'Compare survival rates of selected patients against the remaining population in a Kaplan Meier plot.'
            }, {
                name: 'Patient History',
                route: 'history',
                img: 'history.png',
                copy: ''
            }, {
                name: 'MetaData',
                route: 'metadata',
                img: 'metadata.png',
                copy: ''
            }];
            vm.explore = function(tool, datasource) {
                $state.go(tool, {
                    datasource: datasource
                });
            };
        }
    }

})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osTimelines', timelines);

    /** @ngInject */
    function timelines() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/timelines/timelines.html',
            controller: TimelinesController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function TimelinesController(osApi, $state, $scope, $stateParams, $window, moment, d3) {

            var chart =(function(){

                // SVG Elements + Attributes
                var svgEl = angular.element("#timelines-chart");
                var svgWidth = svgEl.width()-100;
                var svgHeight = svgEl.height();
                var svgChart = d3.select('#timelines-chart').append("svg").attr("width", "100%").attr("height", svgHeight);
                var svgRange = [0, svgWidth];

                // Axis Elements + Style
                var axisStyle = {'shape-rendering': 'crispEdges','stroke': 'none'};
                svgChart.append("g").style(axisStyle)
                    .attr({
                        "class":"axisFeature axis",
                        "transform": "translate(50, 49)"
                    }).call(d3.svg.axis().scale(d3.scale.linear().domain([0, 1]).range([0, svgWidth])).orient('top'));
                svgChart.append("g").style(axisStyle)
                    .attr({
                        "class":"axisTimeline axis",
                        "transform": "translate(50, 0)"
                    }).call(d3.svg.axis().scale(d3.scale.linear().domain([0, 1]).range([0, svgWidth])).orient('bottom'));

                var barBackground = svgChart.append("g");
                var barsFeature = svgChart.append("g");
                var barsTimeline = svgChart.append("g");


                // Main Draw Function
                var draw = function(patientsX, feature, events, sort, align){
                    
                    // Filter Patients That Don't Have Alignment Event
                    var patients = patientsX.filter(function(patient){
                        return patient.hasOwnProperty("__"+this.name);
                    }, align);

                    // Sort Patients
                    patients = patients.sort(function(a,b){
                        return (a.calcEvents[sort.index].value>b.calcEvents[sort.index].value) ? -1 : 1;
                    }, sort);

                    // Set Event Start + End Dates Based On Alignment Event
                    patients.forEach(function(patient){
                        patient.dateEvents.forEach(function(evt){

                            evt.startValue = evt.start + this;
                            evt.endValue = (evt.end!=null) ? evt.end +this : null;
                        }, -patient["__"+this.name].start)

                    }, align);

                    // Draw Graphs
                    var rowHeight = Math.floor( (svgHeight-100) / patients.length);
                    if (rowHeight<1) rowHeight = 1;
                    if (rowHeight>20) rowHeight = 20;
                    
                    drawBackground(patients, feature, events, sort, align, rowHeight);
                    drawFeature(patients, feature, events, sort, align, rowHeight);
                    drawTimeline(patients, feature, events, sort, align, rowHeight);
                };


                var drawFeature = function(patients, feature, events, sort, align, rowHeight){
                    var svgFeatures;
                    if (feature.index==-1){
                        svgFeatures = svgChart.selectAll("rect.feature")
                        .data([]);
                        svgFeatures.exit()
                        .remove();
                        return;
                    }
                    

                    // Retrieve Min + Max Values
                    var minmax = [ 0,
                        d3.max(
                            patients.map(function(patient){
                                return patient.calcEvents[this].value
                            }, feature.index))
                    ];

                    // Update Axis
                    var scale = d3.scale.linear()
                        .domain(minmax)
                        .range(svgRange);                    
                    svgChart.transition().duration(750).select('g.axisFeature').call(
                        d3.svg.axis().scale(scale).orient('top'));
                    
                    // Data Bind
                    svgFeatures = barsFeature.selectAll("rect.feature")
                        .data(patients);

                    // Update
                    svgFeatures
                        .transition()
                        .duration(300)
                         .attr({
                            "class": "feature",
                            "x": 0,
                            "width": function(d) { return scale(d.calcEvents[feature.index].value); },
                            "height": (rowHeight==1) ? 1 : rowHeight-1
                        });

                    // Create
                    svgFeatures
                        .enter()
                        .append("rect")
                        .style( {"fill": "#9AD6F1"})
                        .attr({
                            "transform": function(d, i) {return "translate(50," + ((i * rowHeight)+50) + ")";},
                            "class": "feature",
                            "x": 0,
                            "width": function(d) {
                                return scale(d.calcEvents[feature.index].value);
                            },
                            "height": (rowHeight==1) ? 1 : rowHeight-1
                        })

                    // Remove
                    svgFeatures.exit()
                      .remove();
                };

                var drawBackground = function(patients, features, events, sort, align, rowHeight){
                    angular.element(".timelines-lbl-events").css({"top": (rowHeight * patients.length) + 100 });
                    
                    // Data Bind
                    var svgFeatures = barBackground.selectAll("rect.lines")
                        .data(patients);

                    // Create
                    svgFeatures
                        .enter()
                        .append("rect")
                        .style({'fill':'#EFEFEF'})
                        .attr({
                                'width' : svgWidth,
                                'height' : rowHeight-1,
                                'class' : 'lines',
                                'transform': function(d, i) {
                                 return "translate(50," + ((i * rowHeight)+50) + ")"; }
                            });

                    // Remove
                    svgFeatures
                        .exit()
                        .remove();
                }

                var drawTimeline = function(patients, features, events, sort, align, rowHeight){

                    // Used To Filter Events
                    var filterEventTypes = events.filter( function(events) { return events.selected; });
                    var filterDateEvents = function(dateEvents){
                        return dateEvents.filter(function(dateEvent){
                            for (var i=0; i<filterEventTypes.length; i++){
                                if (filterEventTypes[i].name == dateEvent.name) return true;
                            }
                            return false;
                        });
                    };

                    // Retrive Min + Max Values
                    var minmax = [
                        patients
                        .reduce(function(p, c) {
                            return Math.min(p, filterDateEvents(c.dateEvents)
                                .reduce(function(p, c) {
                                        return Math.min(c.startValue, p)
                                    },
                                    Infinity))
                        }, Infinity),

                        patients
                        .reduce(function(p, c) {
                            return Math.max(p, filterDateEvents(c.dateEvents)
                                .reduce(function(p, c) {
                                    var val = (c.endValue==null) ? c.startValue : c.endValue;
                                    return Math.max(val, p)
                                }, -Infinity))
                        }, -Infinity)
                    ];

                    // Update Axis
                    var scale = 
                        d3.scale.linear()
                            .domain(minmax)
                            .range(svgRange);
                        var axis = svgChart.transition().duration(300).select('g.axisTimeline');
                        axis.attr({"transform": "translate(50,"+((rowHeight*patients.length)+49)+")"});
                        axis
                            .call(d3.svg.axis().scale(scale).orient('bottom')
                            .tickFormat(function(d) { return moment.unix(d/1000).format("MM/DD/YYYY"); })
                        );
                      
                    // Data Bind Patient
                    var rows = barsTimeline.selectAll("g.timeline")
                        .data( patients );

                    rows.enter().append("g")
                        .attr({
                                'width' : svgWidth,
                                'height' : rowHeight-1,
                                'class' : 'timeline',
                                'transform': function(d, i) { return "translate(50," + ((i * rowHeight)+50) + ")"; }
                            });

                    rows.exit().remove();

                    // Data Bind Event
                    var cells = rows.selectAll("rect.timeline")
                        .data( function(d) { return filterDateEvents(d.dateEvents); });

                    cells.enter()
                        .append("rect")
                        .style({'opacity':1, 'fill':color})
                        .attr({
                            'class':'timeline',
                            'height': rowHeight,
                            'width':function(d){ return (d.endValue==null) ? 3 : (scale(d.endValue) - scale(d.startValue)); },
                            'x': function(d) { return Math.round(scale(d.startValue));}
                        })

                    cells.transition()
                        .style({'opacity':1, 'fill':color})
                        .attr({
                            'width':function(d){ return (d.endValue==null) ? 3 : (scale(d.endValue) - scale(d.startValue)); },
                            'x': function(d) { return Math.round(scale(d.startValue));} 
                        });

                    cells.exit().remove();

                }
                return {
                    draw:draw
                }
            })();

            if (angular.isUndefined($stateParams.datasource)){
                $state.go("datasource");
                return;
            }

            // View Model
            var vm = this;
            vm.datasource = $stateParams.datasource;
            vm.features;
            vm.feature;
            vm.events;
            vm.sorts;
            vm.sort;
            vm.align;

            // Static Model
            var rawdata;
            var pfApi = osApi.getPatientFilterApi();
            pfApi.init(vm.datasource);
            pfApi.onSelect.add(draw);

            // Event Handlers
            vm.eventClick = function(eventType) {
                eventType.selected = !eventType.selected;
                draw();
            };
            vm.toggleFilter = function() {
                angular.element(".container-filters").toggleClass("container-filters-collapsed");
                angular.element(".container-filter-toggle").toggleClass("container-filter-toggle-collapsed");
            };

            // Global Refresh Function
            function draw(){ 
                var data = pfApi.filter(rawdata, function(p){ return p.id });
                chart.draw(data, vm.feature, vm.events, vm.sort, vm.align);
            }

            // Elements
            osApi.setBusy(true)("Loading Dataset");
            osApi.setDataset(vm.datasource).then(function() {
                osApi.getTimelines().then(function(response) {

                    // Clean Data + Set Default VM
                    rawdata = processPatientData(response.payload.pts);
                    vm.sorts = processFeatureData(rawdata);
                    vm.sort = vm.sorts[0];
                    var features = processFeatureData(rawdata);
                    features.unshift({index:-1, name:'None'});
                    vm.features =  features;
                    vm.feature = vm.features[0];
                    
                    vm.events = processEventData(response.payload.eventTypes);
                    vm.align = vm.events[0];

                    // Trigger Redraw on Property Change
                    $scope.$watchGroup(['vm.feature', 'vm.sort', 'vm.align'], draw);
                    osApi.setBusy(false);
                });
            });

            // Color Mapping
            var color = function(d){
                var status = d.name;
                var rv = 
                    (status==="Birth") ?  "DARKBLUE" : 
                    (status==="Diagnosis") ? "DARKGREEN" :
                    (status==="Pathology") ? "DARKMAGENTA" :
                    (status==="Progression") ? "DARKORANGE" :
                    (status==="Absent") ? "DARKSLATEBLUE" :
                    (status==="Status") ? "DEEPPINK" :
                    (status==="Radiation") ? "LIMEGREEN" :
                    (status==="Procedure") ? "GOLD" :
                    (status==="Encounter") ? "MIDNIGHTBLUE" :
                    (status==="Drug") ? "MEDIUMSEAGREEN" :
                    "black";
                    return rv;
            };   

            // Data Clean Up Functions
            var processEventData = function(events){
                return Object.keys(events).map(function(v) {
                        var rv =  { "name": v, "selected": true };
                        rv.color = color(rv);
                        return rv;
                });
            }
            var processFeatureData = function(patients){
                return patients[0].calcEvents.map(function(d, i) {
                    return { "name": d.name, "index": i }
                });
            }
            var processPatientData = function(patients){

                 // Load & Normalize Data (Should be done on server)
                patients = Object.keys(patients).map(function(key) {
                    var val = patients[key];
                    val.id = key;
                    return val;
                });
                for (var i = 0; i < patients.length; i++) {

                    // Create References For Faster Subsequent Lookups
                    var p = patients[i];
                    var m = moment;
                    var mf = "YYYY-MM-DD"

                    // Map Start Dates
                    var dateSingle =
                        p.dateEvents
                        .filter(function(d) {
                            return d.eventOrder === 'single'
                        })
                        .map(function(d) {
                            return {
                                "start": m(d.date, mf).unix() * 1000,
                                "end": null,
                                "name": d.name
                            };
                        });

                    // Map Start + End Dates
                    var endDates = p.dateEvents
                        .filter(function(d) { return d.eventOrder==='end' })
                        .map(function(d){ return {"end":m(d.date, mf).unix()*1000, "id":d.eventID}; });
                    var startDates = p.dateEvents
                        .filter(function(d) { return d.eventOrder==='start' })
                        .map(function(d){ return {"start":m(d.date, mf).unix()*1000,"name":d.name, "id":d.eventID}; });
                    startDates
                        .forEach(function(sd){ sd.end = endDates
                            .filter(function(ed) { return ed.id==sd.id} )[0].end });

                    // Join Single + Multiple Date Events
                    p.dateEvents = (startDates.length>0) ? startDates.concat(dateSingle) : dateSingle;
                    p.dateEvents.forEach(function(d){ p["__"+d.name] = d; });
                }
                return patients;
            }
        }
    }
})();
(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osSurvival', survival);

    /** @ngInject */
    function survival() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/survival/survival.html',
            controller: SurvivalController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function SurvivalController(osApi, $state, $timeout, $scope, $stateParams) {

            if (angular.isUndefined($stateParams.datasource)){
                $state.go("datasource");
                return;
            }
            // View Model
            var vm = this;
            vm.datasource = $stateParams.datasource;
            
            // Filter
            var rawData;
            var pfApi = osApi.getPatientFilterApi();
            pfApi.init(vm.datasource);
            pfApi.onSelect.add(draw);

            // Load Datasets
            osApi.setBusy(true);
            osApi.setDataset(vm.datasource).then(function() {
                osApi.getPatientHistoryTable(vm.datasource).then(function(response) {
                    rawData = response.payload.tbl.map( function (d) { return d[0]; });
                    draw();
                });
            });

            // Draw
            function draw(){
                osApi.setBusy(true);
                var ids = pfApi.filter(rawData, function(p){ return p; });
                osApi.getCalculatedSurvivalCurves(ids, "").then(function(r){
                        angular.element("#survival-img").attr('src',r.payload);
                        osApi.setBusy(false);
                });
            }
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osSubnav', login);


    /** @ngInject */
    function login() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/subnav/subnav.html',
            replace: true,
            controller: SubnavController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function SubnavController(osApi, $state) {
            
            var vm = this;
            vm.tool = "Pathways";
            vm.filter = "DemoDZ";

            vm.toolsClick = function(){
                $state.go("tools", {
                    datasource: vm.datasource
                });
            };
            
            vm.cohortClick = function() {
                osApi.toggleFilter();
            };
        }
    }

})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osPlsr', explore);

    /** @ngInject */
    function explore() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/plsr/plsr.html',
            controller: PlsrController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function PlsrController(osApi, $state, $stateParams, $timeout, $scope, d3, $sce) {

            if (angular.isUndefined($stateParams.datasource)){
                $state.go("datasource");
                return;
            }

            // View Model
            var vm = this;
            vm.datasource = $stateParams.datasource;
            vm.deathMinFilter = vm.deathMinValue = 45;
            vm.deathMaxFilter = vm.deathMaxValue = 66;
            vm.survivalMinFilter = vm.survivalMinValue = 3;
            vm.survivalMaxFilter = vm.survivalMaxValue = 7;
            vm.geneSets = [];
            vm.geneSet = null;
            vm.frame;
            vm.tip = null;

            // VM Event Handlers
            vm.toggleFilter = function() {
                angular.element(".container-filters").toggleClass("container-filters-collapsed");
                angular.element(".container-filter-toggle").toggleClass("container-filter-toggle-collapsed");
            };
            vm.update = function() {
                update();
            };

            // Elements
            var elChart = angular.element("#plsr-chart");

            // Chart
            var chart = (function() {


                // Size
                var margin = {
                    top: 20,
                    right: 20,
                    bottom: 30,
                    left: 40
                };
                var width = elChart.width() - margin.left - margin.right;
                var height = elChart.height() - margin.top - margin.bottom;

                var svg = d3.select("#plsr-chart").append("svg")
                    .attr("id", "chart")
                    .attr("width", width)
                    .attr("height", height);

                var tooltip = svg.append("div")
                    .style("position", "absolute")
                    .style("z-index", "10")
                    .style("visibility", "hidden");

                var lines, circles, text;
                var xScale, yScale;

                function draw(abs, vectors, genes) {
                    
                    var nAbs = -1.0 * abs;

                    xScale = d3.scale.linear().domain([nAbs, abs]).range([0, width])
                    yScale = d3.scale.linear().domain([nAbs, abs]).range([height, 0])

                    text = svg.selectAll("text").data(vectors);

                    text
                        .enter().append("text")
                        .attr({
                            "class": "text",
                            "x": function(v) { return xScale(v[0]); },
                            "y": function(v) { return yScale(v[1]); },
                            "text-anchor": function(v) { return (v[0] > 0) ? "start" : "end" }
                        })
                        .text(function(v) { return v.name; })
                        .style("fill", "black");

                    text
                        .transition()
                        .duration(900)
                        .attr({
                            "x": function(v) { return xScale(v[0]); },
                            "y": function(v) { return yScale(v[1]); }
                        });

                    text
                        .exit()
                        .remove();
                        

                    lines = svg.selectAll("line").data(vectors)

                    lines
                        .enter()
                        .append("line")
                        .attr({
                            "class": "line",
                            "stroke-width": 3,
                            "x1": xScale(0),
                            "y1": yScale(0),
                            "x2": function(v) { return xScale(v[0]); },
                            "y2": function(v) { return yScale(v[1]); }
                        })
                        .style("stroke", function(d) {
                            return (d.name.indexOf("Age")) ? "#1396de" : "#38347b"
                        });

                    lines
                        .transition()
                        .duration(900)
                        .attr("x1", xScale(0))
                        .attr("y1", yScale(0))
                        .attr("x2", function(v) {
                            return xScale(v[0]);
                        })
                        .attr("y2", function(v) {
                            return yScale(v[1]);
                        });

                    lines
                        .exit().remove();
                  
                    circles = svg.selectAll("circle").data(genes);

                    circles
                        .enter()
                        .append("circle")
                        .attr({
                            "cx": function(d) { return xScale(d[0]); },
                            "cy": function(d) { return yScale(d[1]); },
                            "r": 3
                        })
                        .style({
                            'fill': 'black',
                            'opacity': 1
                        })
                        .on("click", function(d) {
                            angular.element('#plsr-webpage').modal();
                            var url = "http://www.genecards.org/cgi-bin/carddisp.pl?gene=" + d.name;
                            $scope.$apply(function() {
                                vm.frame = $sce.trustAsResourceUrl(url);
                            });
                        })
                        .on("mouseover", function(d) {
                            var pt = d3.mouse(this);
                            tooltip
                                .text(d.name)
                                .style("top", (pt[1] + 160) + "px")
                                .style("left", (pt[0] + 10) + "px")
                                .style("visibility", "visible");
                        })
                        .on("mousemove", function() {
                            var pt = d3.mouse(this);
                            tooltip
                                .style("top", (pt[1] + 160) + "px")
                                .style("left", (pt[0] + 10) + "px");
                        })
                        .on("mouseout", function() {
                            return tooltip.style("visibility", "hidden");
                        })


                    circles
                        .transition()
                        .duration(900)
                        .each("start", function() { d3.select(this) })
                        .delay(function(d, i) {
                            return i / genes.length * 500; // Dynamic delay (i.e. each item delays a little longer)
                        })
                        .attr("cx", function(d) { return xScale(d[0]);  })
                        .attr("cy", function(d) { return yScale(d[1]);  })
                        .each("end", function() { // End animation
                            d3.select(this) // 'this' means the current element
                                .transition()
                                .duration(500)
                                .style("fill", "black") // Change color
                                .attr("r", 3); // Change radius
                        })

                    circles
                        .exit().remove();

                }

                function update(abs, vectors, genes) {

                    text
                        .data(vectors)
                        .transition()
                        .duration(900)
                        .attr("x", function(v) {
                            return xScale(v[0]);
                        })
                        .attr("y", function(v) {
                            return yScale(v[1]);
                        })



                    lines
                        .data(vectors)
                        .transition()
                        .duration(900)
                        .attr("x1", xScale(0))
                        .attr("y1", yScale(0))
                        .attr("x2", function(v) {
                            return xScale(v[0]);
                        })
                        .attr("y2", function(v) {
                            return yScale(v[1]);
                        });


                    circles
                        .data(genes); // Update with new data
                        

                    circles
                        .transition()
                        .duration(900)
                        .each("start", function() { // Start animation
                            d3.select(this) // 'this' means the current element
                                //.style("fill", "000")  // Change color
                                //.attr("r", 2);  // Change size
                        })
                        .delay(function(d, i) {
                            return i / genes.length * 500; // Dynamic delay (i.e. each item delays a little longer)
                        })
                        //.ease("linear")  // Transition easing - default 'variable' (i.e. has acceleration), also: 'circle', 'elastic', 'bounce', 'linear'
                        .attr("cx", function(d) {
                            return xScale(d[0]); // Circle's X
                        })
                        .attr("cy", function(d) {
                            return yScale(d[1]); // Circle's Y
                        })
                        .each("end", function() { // End animation
                            d3.select(this) // 'this' means the current element
                                .transition()
                                .duration(500)
                                .style("fill", "black") // Change color
                                .attr("r", 3); // Change radius
                        })
                }



                return {
                    draw: draw

                }
            })("#plsr-chart");




            // Load Data
            osApi.setBusy(true)("Loading Dataset");
            osApi.setDataset(vm.datasource).then(function(response) {
                var mtx = response.payload.rownames.filter(function(v) {
                    return v.indexOf("mtx.mrna") >= 0
                });
                mtx = mtx[mtx.length - 1].replace(".RData", "");
                osApi.setBusyMessage("Creating PLSR Matrix");
                osApi.getPLSR(vm.datasource, mtx).then(function() {
                    osApi.setBusyMessage("Loading Gene Sets");
                    osApi.getGeneSetNames().then(function(response) {

                        // Load Gene Sets
                        vm.geneSets = response.payload;
                        vm.geneSet = vm.geneSets[0];
                        osApi.setBusyMessage("Loading Patients");
                        osApi.getSummarizedPLSRPatientAttributes().then(function(response) {

                            // Load Min Max Values
                            var payload = response.payload;
                            vm.deathMinValue = Math.floor(payload.AgeDx[0] / 365.24);
                            vm.deathMaxValue = Math.floor(payload.AgeDx[4] / 365.24);
                            vm.survivalMinValue = Math.floor(payload.Survival[0] / 365.24);
                            vm.survivalMaxValue = Math.floor(payload.Survival[4] / 365.24);
                            $scope.$watch('vm.geneSet', function() {
                                update();
                            });
                        });
                    });
                });
            });


            // API Call To Calculate PLSR
            var update = function() {
                osApi.setBusyMessage("Calculating PLSR");
                var factors = [{
                    name: "Survival",
                    low: Number(vm.survivalMinFilter) * 365.24,
                    high: Number(vm.survivalMaxFilter) * 365.24
                }, {
                    name: "AgeDx",
                    low: Number(vm.deathMinFilter) * 365.24,
                    high: Number(vm.deathMaxFilter) * 365.24
                }];

                osApi.getCalculatedPLSR(vm.geneSet, factors).then(function(response) {
                    osApi.setBusyMessage("Rendering PLSR");

                    // Clean Up Data
                    var payload = response.payload;
                    var genes = payload.loadings.map(function(item, index) {
                        item.name = payload.loadingNames[index];
                        return item;
                    });
                    var vectors = payload.vectors.map(function(item, index) {
                        item.name = payload.vectorNames[index];
                        return item;
                    });
                    var abs = payload.maxValue * 1.2;


                    chart.draw(abs, vectors, genes);
                    
                    osApi.setBusy(false);
                });
            };

        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osPca', explore);

    /** @ngInject */
    function explore() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/pca/pca.html',
            controller: PcaController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function PcaController(osApi, $state, $stateParams, $timeout, $scope, d3) {

            if (angular.isUndefined($stateParams.datasource)){
                $state.go("datasource");
                return;
            }

            // State
            var vm = this;
            vm.datasource = $stateParams.datasource;
            vm.geneSets = [];
            vm.geneSet = null;
            vm.toggleFilter = function() {
                angular.element(".container-filters").toggleClass("container-filters-collapsed");
                angular.element(".container-filter-toggle").toggleClass("container-filter-toggle-collapsed");
            }

            // Filters
            var rawData;
            var pfApi = osApi.getPatientFilterApi();
            pfApi.init(vm.datasource);
            pfApi.onSelect.add(draw);

            vm.cohort;
            vm.createCohort = function() {
                pfApi.addFilter(vm.cohort, d3.selectAll(".pca-node-selected")[0].map(function(data) { return data.__data__.id }) );
                vm.cohort = "";
            };


            // Elements
            var elChart = angular.element("#pca-chart");
            var d3Chart = d3.select("#pca-chart")
                .append("svg")
                .attr("id", "chart");

            var d3xAxis = d3Chart.append("g");
            var d3yAxis = d3Chart.append("g");
            
            var d3Tooltip = d3.select("body")
                .append("div")
                .attr("class", "tooltip")


            // Initalizae
            osApi.setBusy(true)("Loading Dataset");
            osApi.setDataset(vm.datasource).then(function(response) {
                var mtx = response.payload.rownames.filter(function(v) {
                    return v.indexOf("mtx.mrna") >= 0
                });

                mtx = mtx[mtx.length - 1].replace(".RData", "");
                osApi.setBusyMessage("Creating PCA Matrix");
                osApi.getPCA(vm.datasource, mtx).then(function() {
                    osApi.setBusyMessage("Loading Gene Sets");
                    osApi.getGeneSetNames().then(function(response) {

                        // Load Gene Sets
                        vm.geneSets = response.payload;
                        vm.geneSet = vm.geneSets[0];
                        $scope.$watch('vm.geneSet', function() {
                            update();
                        });
                    });
                });
            });

            // API Call To Calculate PCA
            var update = function() {
                osApi.setBusyMessage("Calculating PCA");
                osApi.getCalculatedPCA(vm.geneSet).then(function(response) {
                    osApi.setBusyMessage("Rendering PCA");
                    var payload = response.payload;
                    vm.pc1 = response.payload["importance.PC1"];
                    vm.pc2 = response.payload["importance.PC2"];
                    var scores = payload.scores;
                    var ids = payload.ids;
                    rawData = scores.map(function(d, i){
                        d.id = ids[i];
                        return d;
                    }, payload.ids);
                    draw()
                    osApi.setBusy(false);
                });
            };

            // Render
            function draw() {
                
                var dataset = rawData;

                var width = elChart.width();
                var height = elChart.height();

                var max, min;
                max = Math.abs(d3.max(dataset, function(d) {
                    return +d[0];
                }));
                min = Math.abs(d3.min(dataset, function(d) {
                    return +d[0];
                }));
                var xMax = ((max > min) ? max : min) * 1.2;
                max = Math.abs(d3.max(dataset, function(d) {
                    return +d[1];
                }));
                min = Math.abs(d3.min(dataset, function(d) {
                    return +d[1];
                }));
                var yMax = ((max > min) ? max : min) * 1.2;

                var xScale = d3.scale.linear()
                    .domain([-xMax, xMax])
                    .range([0, width]);

                var yScale = d3.scale.linear()
                    .domain([-yMax, yMax])
                    .range([height, 0]);

                var xAxis = d3.svg.axis()
                    .scale(xScale)
                    .orient("top")
                    .ticks(5);

                var yAxis = d3.svg.axis()
                    .scale(yScale)
                    .orient("left")
                    .ticks(5);

                // Brush
                var brush = d3.svg.brush()
                   .x(xScale)
                   .y(yScale)
                   .on("brushend", function(){
                        var bv = brush.extent();
                        d3Chart.selectAll("circle")
                            .classed("pca-node-selected", function(d){
                                return (d[0] > bv[0][0] && d[0] < bv[1][0] && d[1] > bv[0][1] && d[1] < bv[1][1]);                                
                            });
                        d3.select(this).transition().duration(300)
                            .call( brush.extent([[0,0],[0,0]]) );
                   });

                d3Chart.call(brush);

                dataset = pfApi.filter(rawData, function(p){ return p.id });
                
                d3Chart
                    .attr("width", width)
                    .attr("height", height);

                var circles = d3Chart.selectAll("circle").data(dataset, function(d) {
                    return d;
                })
                circles.enter()
                    .append("circle")
                    .attr("class","pca-node")
                    .attr("cx", width * .5)
                    .attr("cy", height * .5)
                    .attr("r", function() {
                        return 3;
                    })
                    .style("fill-opacity", "0")
                    .on("mouseover", function(d) {
                        d3Tooltip.transition()        
                            .duration(200)      
                            .style("opacity", 1);      
                        d3Tooltip.html(d.id)  
                            .style("left", (d3.event.pageX+10) + "px")     
                            .style("top", (d3.event.pageY-5) + "px");    
                    })                  
                    .on("mouseout", function(){
                        d3Tooltip.transition()      
                            .duration(500)      
                            .style("opacity", 0);   
                    })
                    .transition()
                    .duration(750)
                    .delay(function(d, i) {
                        return i / 300 * 500;
                    })
                    .attr("cx", function(d) {
                        return xScale(d[0]);
                    })
                    .attr("cy", function(d) {
                        return yScale(d[1]);
                    })
                    .style("fill-opacity", 1)
                    

                circles.exit()
                    .transition()
                    .duration(600)
                    .delay(function(d, i) {
                        return i / 300 * 500;
                    })
                    .attr("cx", width * .5)
                    .attr("cy", height * .5)
                    .style("fill-opacity", "0")
                    .remove();


                d3xAxis
                    .attr("class", "axis")
                    .attr("transform", "translate(0, " + yScale(0) + ")")
                    .call(xAxis)
                    .append("text")
                    .text("PC1");

                d3yAxis
                    .attr("class", "axis")
                    .attr("transform", "translate(" + xScale(0) + ", 0)")
                    .call(yAxis)
                    .append("text")
                    .attr("y", 10)
                    .attr("dy", ".71em")
                    .text("PC2");
            }
        }
    }
})();

